}
.layer-menu-outer {
  padding: 25px;
  width: 240px;
}

.layer-menu {
  border-color: #333;
  border-width: thin;
  border-style: solid;
}

.ll {
  padding: 4px 4px 2px 2px;
  font-size: 0.9rem;
  line-height: 1.1rem;
}

.layer-group {
  padding-left: 5px;
  background-color: rgba(0, 0, 0, 0.1);
  border-color: #aaa;
  border-width: 0 0 1px 0;
  border-style: solid;
}

.layer-sub-group {
  padding-left: 10px;
  background-color: rgba(0, 0, 0, 0.03);
  border-color: #aaa;
  border-width: 0 0 1px 0;
  border-style: solid;
}

.layer {
  padding-left: 28px;
  border-color: #ddd;
  border-width: 0 0 1px 0;
  border-style: solid;
}
.overlay {
  position: fixed;
  display: none;
  width: 100%;
  height: 100%;
  top: 66px;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0,0,0,0.5);
  z-index: 2;
  cursor: pointer;
}

.ol_tmp {
